body {
  --active-color: #00acdc;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.logo_img {
  display: flex;
}

li {
  display: inline-block;
}

.nav__links {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  margin-right: 20px;
  text-decoration: none;
  list-style-type: none;
  display: flex;
  position: relative;
}

.nav__links:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active:hover:after {
  height: 4px;
  bottom: 0;
}

li:last-child > .nav__links {
  margin-right: 0;
}

